.highlight-item {
  width: 100%;
  display: flex;
  height: 125px;
  margin-bottom: 15px;

  .item-details {
    width: 70%;
    display: flex;

    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 10px 20px;
    .type {
      padding: 2px 5px;
      margin: 2px;
      cursor: pointer;
      background-color: black;
      color: white;
      border-radius: 5px;
    }
    .name {
      font-size: 16px;
    }
  }
  .icon {
    width: 20px !important;
    height: 20px !important;
    cursor: pointer;
    display: grid;
    justify-content: space-between;
  }
}
