.header {
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  margin-bottom: 25px;

  .Message {
    color: black;
  }

  .options {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: right;
    justify-content: flex-end;

    .option {
      padding: 10px 15px;
      margin: 10px;
      cursor: pointer;
      background-color: black;
      color: white;

      border-radius: 10px;
      .link {
        color: white;
      }
    }
  }
}
