.group {
  display: flex;
  margin: 5px;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 95%;
  .name {
    display: flex;
    justify-content: center;
  }
  .container {
    margin: 10px;
    width: 45%;
    height: 100%;
    border-radius: 10px;
    background-color: #dbe7c5;
    display: block;
    justify-content: flex-start;

    .highlights {
      margin: 10px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;

      .item {
        width: 50%;
        min-width: 250px;
      }
    }
  }
}

.zoomIn {
  transition: transform 0.2s;
  &:hover {
    transform: scale(1.5);
  }
}
